<template>
  <div class="tableBox">
    <div class="addProd">
      <el-button type="primary" @click="addProd">创建新产品</el-button>
      <div class="search">
        <el-input v-model="keywords" placeholder="请输入产品号或任务名" @input="search">
          <i
               v-show="keywords"
               class="el-icon-close"
               style="margin-top:12px"
               slot="suffix"
               @click="clearInput">
          </i>
        </el-input>
      </div>
    </div>
    <el-dialog
      :visible.sync="newProdDialogVisible"
      width="30%"
      :before-close="handleClose">
      <el-input v-model="createProd.prodModel" placeholder="请输入产品型号"></el-input>
      <el-input v-model="createProd.prodStage" placeholder="请输入产品阶段"></el-input>
      <el-input v-model="createProd.prodDirector" placeholder="请输入产品负责人"></el-input>
      <el-form
           style="padding-top: 20px"
           ref="form"
           :model="form"
           :rules="rules">
        <el-form-item  prop="file" >
          <el-upload
            class="uploadProdExcel"
            ref="upload"
            accept=".xlsx,.xls"
            :auto-upload="false"
            action="action"
            :http-request="uploadOk" 
            :on-change="onChange"
            :on-exceed="handleExceed"
            :before-remove="removefile"
            :limit="1"
            :file-list="fileListAdd"
            v-model="form.file">
            <el-button size="small" type="primary"  @click="clearCheck">上传Excel文件关联工序</el-button>
            <div slot="tip" class="el-upload__tip">只能上传excel文件，且不超过10M</div>
          </el-upload>
        </el-form-item>
        </el-form>
        <el-button @click="submit" type="warning" align="center">保存</el-button>
    </el-dialog>
    <el-table
      :header-cell-style="{background:'#ccc',color:'#000'}"
      :data="prodList"
      style="width:100%;"
      row-key="id"
      border
      stripe
      :tree-props="{children: 'children', hasChildren: 'hasChildren'}">
      <el-table-column
        prop="prodModel"
        align="center"
        label="产品型号">
      </el-table-column>
      <el-table-column
        prop="prodCreateDay"
        align="center"
        width="150"
        label="产品创建日期">
      </el-table-column>
      <el-table-column
        prop="prodStatus"
        align="center"
        label="产品状态">
      </el-table-column>
      <el-table-column
        prop="prodStage"
        align="center"
        label="产品阶段">
      </el-table-column>
      <el-table-column
        prop="taskCreatDay"
        align="center"
        width="150"
        label="任务创建日期">
      </el-table-column>
      <el-table-column
        prop="taskName"
        align="center"
        label="任务名称">
      </el-table-column>
      <el-table-column
        prop="operator"
        align="center"
        label="操作员">
      </el-table-column>
      <el-table-column
        prop="taskStatus"
        align="center"
        label="任务状态">
      </el-table-column>
      <el-table-column
        prop="ifsend"
        align="center"
        label="下发状态">
      </el-table-column>
      <el-table-column
        prop="planDay"
        align="center"
        label="任务计划完成天数">
      </el-table-column>
      <el-table-column
        prop="realDay"
        align="center"
        label="任务实际完成天数">
      </el-table-column>
      <el-table-column label="操作" width="330" align="center">
        <template slot-scope="scope" >
          <el-button @click="delProd(scope.row)" v-if="scope.row.children"  size="mini" type="danger">删除产品</el-button>
          <el-dialog
            title="提示"
            :visible.sync="delProdDialogVisible"
            width="30%"
            append-to-body
            :before-close="handleClose">
            <span>确认删除该产品？</span>
            <span slot="footer" class="dialog-footer">
              <el-button @click="delProdDialogVisible = false">取 消</el-button>
              <el-button @click="delProdSure" type="primary">确 定</el-button>
            </span>
          </el-dialog>
          <el-button @click="sendMissionBtn(scope.row)" v-if="scope.row.children && scope.row.ifsend == '未下发' "  size="mini" type="warning">下发产品任务</el-button>
          <el-dialog
            title="提示"
            :visible.sync="sendMissionDialogVisible"
            width="30%"
            append-to-body
            :before-close="handleClose">
            <span>确认下发产品任务？</span>
            <span slot="footer" class="dialog-footer">
              <el-button @click="sendMissionDialogVisible = false">取 消</el-button>
              <el-button @click="sendProdMissionSure" type="primary">下 发</el-button>
            </span>
          </el-dialog>
          <el-button @click="checkTaskSheetBtn(scope.row)" v-if="scope.row.children && scope.row.ifsend == '已下发'"  size="mini" type="primary">查看检验单</el-button>
          <el-dialog custom-class="checkSheetDialog"  append-to-body title="产品任务检验单" :visible.sync="checkTaskDialogVisible" :before-close="handleClose">
            <el-table  stripe border fit highlight-current-row :data="checkTaskList">
              <el-table-column align="center" type="index" width="60" label="序号"></el-table-column>
              <el-table-column  align="center"  prop="prodModel" label="产品型号"></el-table-column>
              <el-table-column  align="center"  prop="prodStage" label="产品阶段"></el-table-column>
              <el-table-column  align="center"  prop="taskName" label="生产工序"></el-table-column>
              <el-table-column  align="center"  prop="taskStatus" label="任务状态">已完成</el-table-column>
              <el-table-column  align="center"  prop="taskCreatday" label="任务创建日期"></el-table-column>
            </el-table>
          </el-dialog>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <el-pagination
      background
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="pageSize"
      :total="totalDataNum"
      layout="total,prev, pager, next">
    </el-pagination>
  </div>
</template>
<script>
import {DelProd,SendMissions,PlannerCheckTaskSheet,UploadFile,PlannerProAndMisList} from '@/api/request'
import { Message } from 'element-ui'
export default {
        data() {
          return {
            timer:null,
            form:{file:''},
            fileListAdd:[],
            rules: {
              file: [
                {
                  required: true,
                  message: '请选择文件上传',
                  trigger: 'change'
                }
              ]
            },  
            createProd:{
              prodModel:'',
              prodStage:'',
              prodDirector:'',
            },
            totalDataNum:0,
            keywords:'',
            currentPage:1,
            pageSize:10,
            checkTaskDialogVisible:false,
            newProdDialogVisible:false,
            delProdDialogVisible:false,
            sendMissionDialogVisible:false,
            prodName:'',
            prodList: [ 
            
            ],
            checkTaskList:[
             
            ]
          }
        },
        mounted(){
          this.timer = setInterval(() => {
            this.getProAndMisList()
          }, 1000);
       },
        methods: {
          search(){
            this.currentPage = 1
            this.getProAndMisList()
          },
          clearInput(){
            this.keywords = ''
            this.currentPage = 1
            this.getProAndMisList()
          },
          getProAndMisList(){
            var params ={
              keywords:this.keywords,
              currentPage:this.currentPage,
              pageSize:this.pageSize,
            }
            PlannerProAndMisList(params).then((res)=>{
              this.prodList = res.data.respData.prodListDetail
              this.totalDataNum = res.data.respData.totalDataNum
              for(let i = 0;i < this.prodList.length;i++){
                if(this.prodList[i].ifsend == 1){
                  this.prodList[i].ifsend = '已下发'
                }else{
                  this.prodList[i].ifsend = '未下发'
                }
                if(this.prodList[i].prodStatus == 1){
                  this.prodList[i].prodStatus = '未实施'
                }else if(this.prodList[i].prodStatus == 2){
                  this.prodList[i].prodStatus = '实施中'
                }else if(this.prodList[i].prodStatus == 3){
                  this.prodList[i].prodStatus = '已完成'
                }else if(this.prodList[i].prodStatus == 4){
                  this.prodList[i].prodStatus = '延期'
                }else if(this.prodList[i].prodStatus == 5){
                  this.prodList[i].prodStatus = '停工'
                }
                
                for(let j = 0;j < this.prodList[i].children.length;j++){
                  if(this.prodList[i].children[j].taskStatus == 1){
                    this.prodList[i].children[j].taskStatus = '未实施'
                  }else if(this.prodList[i].children[j].taskStatus == 2){
                    this.prodList[i].children[j].taskStatus = '实施中'
                  }else if(this.prodList[i].children[j].taskStatus == 3){
                    this.prodList[i].children[j].taskStatus = '已完成'
                  }else if(this.prodList[i].children[j].taskStatus == 4){
                    this.prodList[i].children[j].taskStatus = '延期'
                  }else if(this.prodList[i].children[j].taskStatus == 5){
                    this.prodList[i].children[j].taskStatus = '检验中'
                  }else if(this.prodList[i].children[j].taskStatus == 6){
                    this.prodList[i].children[j].taskStatus = '终止'
                  }
                }
              }
            })
          },
          handleSizeChange(SizeChange){ 
          console.log('SizeChange',SizeChange);
          this.pageSize = SizeChange
          this.getProAndMisList()
          },
          handleCurrentChange(CurrentPage){ 
            console.log('CurrentPage',CurrentPage);
            this.currentPage = CurrentPage
            this.getProAndMisList()
          },
          countPageIndex(index){
            return (this.currentPage - 1) * this.pageSize + index + 1
          },
          delProd(row){
            console.log(row);
            this.delProdDialogVisible = true
            this.prodName = row.prodModel
          },
          delProdSure(){
            DelProd({'prodModel':this.prodName}).then((res) =>{
              console.log('删除产品res',res);
              if(res.data.result == 1){
                this.getProAndMisList()
                 Message.success(res.data.msg)
                 this.delProdDialogVisible = false
              }else{
                 Message.error(res.data.msg)
              }
            })
          },
          sendMissionBtn(row){
            console.log('row',row);
            this.sendMissionDialogVisible = true
            this.prodName = row.prodModel
          },
          sendProdMissionSure(){
            SendMissions({'prodModel':this.prodName}).then((res) =>{
              console.log('下发产品任务RES',res);
              if(res.data.result == 1){
                this.getProAndMisList()
                 Message.success(res.data.msg)
                 this.sendMissionDialogVisible = false
              }else{
                Message.error(res.data.msg)
              }
            })
          },
          checkTaskSheetBtn(row){
            console.log('检验单row',row);
            PlannerCheckTaskSheet({'prodModel':row.prodModel}).then((res) =>{
              console.log('查看任务检验单res',res);
              if(res.data.result == 1){
                this.checkTaskDialogVisible = true
                this.checkTaskList = res.data.respData
                for(let i = 0;i < this.checkTaskList.length;i++){
                  if(this.checkTaskList[i].taskStatus == 3){
                    this.checkTaskList[i].taskStatus = '已完成'
                  }
                }
              }else{
                Message.error(res.data.msg)
                this.checkTaskList = []
              }
            })
          },
          handleClose(done){
            done()
            this.fileListAdd = []
            this.createProd.prodModel = ''
            this.createProd.prodStage = ''
            this.createProd.prodDirector = ''
          },
          addProd(){
           this.newProdDialogVisible = true
          },
          // 上传文件相关方法
          handleExceed(files, fileList) {
            this.$message.warning(`只能选择 1个文件，本次选择了 ${files.length} 个文件`);
          },
          //文件与其他输入类型的字段一起提交上传
          uploadOk(file) {
            let creatorID = JSON.parse(sessionStorage.getItem('userID'))
            this.uploaddata = {
              'prodModel':this.createProd.prodModel,
              'prodStage':this.createProd.prodStage,
              'prodDirector':this.createProd.prodDirector,
              'creatorID':creatorID}
            let params = new FormData();
            for (const key in this.uploaddata) {
              params.append(key, this.uploaddata[key]);
            }
            params.append('file', file.file);
            UploadFile(params).then((res) =>{
              console.log('上传res',res);
              if(res.data.result == 1){
                this.getProAndMisList()
                this.newProdDialogVisible = false
                 Message.success(res.data.msg)
                 this.fileListAdd = []
                 this.createProd.prodModel = ''
                 this.createProd.prodStage = ''
                 this.createProd.prodDirector = ''
              }else{
                this.fileListAdd = []
                Message.error(res.data.msg)
              }
            })
          },
          //导入的下一步
          submit() {
            this.$refs['form'].validate((valid) => {
              if (valid) {
                this.$refs.upload.submit();
              } else {
                return false;
              }
            });
          },
          //文件状态改变的函数
          onChange(fileList) {
            this.$set(this.form, 'file', fileList);
          },
          removefile() {
            this.$set(this.form, 'file', '');
          },
          clearCheck() {
            this.$refs.form.clearValidate('file');
          },
       },
       beforeDestroy(){
        clearInterval(this.timer);
        this.timer = null;
       }
}
</script>
<style lang="scss" scoped>
    .tableBox{
        width: 100%;
        // height: 89.9%;
        overflow: scroll;
        .addProd{
          display: flex;
          justify-content: space-between;
          margin-bottom: .9rem;
          .search{
            display: flex;
          }
        }
        .el-table{
          height: 100%;
        }
         .checkSheetDialog{
         width:65% !important;
        }
    }
    
</style>


